ta\n      border-radius: 0 $file-radius $file-radius 0\n    .file-name\n      border-radius: $file-radius 0 0 $file-radius\n      border-width: 1px 0 1px 1px\n      order: -1\n\n.file-label\n  align-items: stretch\n  display: flex\n  cursor: pointer\n  justify-content: flex-start\n  overflow: hidden\n  position: relative\n  &:hover\n    .file-cta\n      background-color: bulmaDarken($file-cta-background-color, 2.5%)\n      color: $file-cta-hover-color\n    .file-name\n      border-color: bulmaDarken($file-name-border-color, 2.5%)\n  &:active\n    .file-cta\n      background-color: bulmaDarken($file-cta-background-color, 5%)\n      color: $file-cta-active-color\n    .file-name\n      border-color: bulmaDarken($file-name-border-color, 5%)\n\n.file-input\n  height: 100%\n  left: 0\n  opacity: 0\n  outline: none\n  position: absolute\n  top: 0\n  width: 100%\n\n.file-cta,\n.file-name\n  @extend %control\n  border-color: $file-border-color\n  border-radius: $file-radius\n  font-size: 1em\n  padding-left: 1em\n  padding-right: 1em\n  white-space: nowrap\n\n.file-cta\n  background-color: $file-cta-background-color\n  color: $file-cta-color\n\n.file-name\n  border-color: $file-name-border-color\n  border-style: $file-name-border-style\n  border-width: $file-name-border-width\n  display: block\n  max-width: $file-name-max-width\n  overflow: hidden\n  text-align: inherit\n  text-overflow: ellipsis\n\n.file-icon\n  align-items: center\n  display: flex\n  height: 1em\n  justify-content: center\n  +ltr-property(\"margin\", 0.5em)\n  width: 1em\n  .fa\n    font-size: 14px\n","$label-color: $text-strong !default\n$label-weight: $weight-bold !default\n\n$help-size: $size-small !default\n\n$label-colors: $form-colors !default\n\n.label\n  color: $label-color\n  display: block\n  font-size: $size-normal\n  font-weight: $label-weight\n  &:not(:last-child)\n    margin-bottom: 0.5em\n  // Sizes\n  &.is-small\n    font-size: $size-small\n  &.is-medium\n    font-size: $size-medium\n  &.is-large\n    font-size: $size-large\n\n.help\n  display: block\n  font-size: $help-size\n  margin-top: 0.25rem\n  @each $name, $pair in $label-colors\n    $color: nth($pair, 1)\n    &.is-#{$name}\n      color: $color\n\n// Containers\n\n.field\n  &:not(:last-child)\n    margin-bottom: 0.75rem\n  // Modifiers\n  &.has-addons\n    display: flex\n    justify-content: flex-start\n    .control\n      &:not(:last-child)\n        +ltr-property(\"margin\", -1px)\n      &:not(:first-child):not(:last-child)\n        .button,\n        .input,\n        .select select\n          border-radius: 0\n      &:first-child:not(:only-child)\n        .button,\n        .input,\n        .select select\n          +ltr\n            border-bottom-right-radius: 0\n            border-top-right-radius: 0\n          +rtl\n            border-bottom-left-radius: 0\n            border-top-left-radius: 0\n      &:last-child:not(:only-child)\n        .button,\n        .input,\n        .select select\n          +ltr\n            border-bottom-left-radius: 0\n            border-top-left-radius: 0\n          +rtl\n            border-bottom-right-radius: 0\n            border-top-right-radius: 0\n      .button,\n      .input,\n      .select select\n        &:not([disabled])\n          &:hover,\n          &.is-hovered\n            z-index: 2\n          &:focus,\n          &.is-focused,\n          &:active,\n          &.is-active\n            z-index: 3\n            &:hover\n              z-index: 4\n      &.is-expanded\n        flex-grow: 1\n        flex-shrink: 1\n    &.has-addons-centered\n      justify-content: center\n    &.has-addons-right\n      justify-content: flex-end\n    &.has-addons-fullwidth\n      .control\n        flex-grow: 1\n        flex-shrink: 0\n  &.is-grouped\n    display: flex\n    justify-content: flex-start\n    & > .control\n      flex-shrink: 0\n      &:not(:last-child)\n        margin-bottom: 0\n        +ltr-property(\"margin\", 0.75rem)\n      &.is-expanded\n        flex-grow: 1